.header-container {
    margin: 0px;
    display: flex;
    flex-direction: column;
}

.header-container img{
    height: 50vh;
    justify-content: center;
}
.search-box{
    display: flex;
    padding: 50px;
    flex-direction: column;
    justify-content: center;
}

.search-box input{
    height: 40px;
    line-height: 30px;
    width: 50%;
    margin-left: 25%;
    text-align: center;
    font-size: 20px;
    color: white;
    background: linear-gradient(rgba(0, 0, 0, 0.418), rgba(255, 255, 255, 0.452));
    font-family: 'Press Start 2P', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    transition: 0.5s;
}

.search-box p{
    text-align: center;
}

.search-box input:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background: linear-gradient(rgba(255, 255, 255, 0.37), rgba(255, 255, 0, 0.425));
}
.people-card{
    border-radius: 25%;
    border: 3px solid white;
    padding-top: 15%;
    padding-bottom: 15%;
    margin: 30px 15px  10px 15px;
    align-content: center;
    text-align: center;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.418), rgba(255, 255, 255, 0.452));
    transition: 1.5s;
}

.people-card h3{
    padding: 10px;
}

.people-card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: linear-gradient(rgba(255, 255, 255, 0.37), rgba(255, 255, 0, 0.425));
}
.search-component-div{
    display: grid;
    background: linear-gradient(rgba(0, 0, 0, 0.418), rgba(255, 255, 255, 0.452));
    grid-template-columns: repeat(3, 1fr);
}
